import { generateAddress2 } from "ethereumjs-util"
import { utils, ContractFactory } from "ethers"
import { Transaction } from "adex-protocol-eth/js"
import { ADDR_FACTORY, ADDR_GASLESS_SWEEPER, useTestnet } from "./constants"
import GaslessDepositorABI from "../abi/GaslessDepositor.json"
import GaslessDepositorBytecode from "../abi/GaslessDepositor_bytecode.json"

function getBytecode(addr) {
	const addrHex = addr.slice(2).toLowerCase()
	// Do not change that bytecode! It comes from compiling this https://gist.github.com/Ivshti/e66cd57ade1abfb07c2517df9e73ceb9

	const bytecode = useTestnet
		? `0x608060405234801561001057600080fd5b5060026000803073ffffffffffffffffffffffffffffffffffffffff1673ffffffffffffffffffffffffffffffffffffffff16815260200190815260200160002060006101000a81548160ff021916908360ff160217905550600260008073${addrHex}`
		: `0x608060405234801561001057600080fd5b5060026000803073ffffffffffffffffffffffffffffffffffffffff1673ffffffffffffffffffffffffffffffffffffffff16815260200190815260200160002060006101000a81548160ff021916908360ff160217905550600260008073${addrHex}`
	return bytecode
}

export function getUserIdentity(walletAddr) {
	const bytecode = getBytecode(walletAddr)
	const addr = utils.getAddress(
		`0x${generateAddress2(ADDR_FACTORY, Buffer.alloc(32), bytecode).toString(
			"hex"
		)}`
	)
	return { bytecode, addr }
}

export function rawZeroFeeTx(idAddr, nonce, to, data) {
	return {
		identityContract: idAddr,
		nonce: nonce.toString(),
		feeTokenAddr: "0x6b175474e89094c44da98b954eedeac495271d0f",
		feeAmount: 0,
		to,
		data
	}
}

export function zeroFeeTx(idAddr, nonce, to, data) {
	return new Transaction({
		identityContract: idAddr,
		nonce: nonce.toString(),
		feeTokenAddr: "0x6b175474e89094c44da98b954eedeac495271d0f",
		feeAmount: 0,
		to,
		data
	})
}

export function getUserGaslessAddress(ADXTokenAddr, poolAddr, walletAddr) {
	const factory = new ContractFactory(
		GaslessDepositorABI,
		GaslessDepositorBytecode.object
	)
	const initCode = factory.getDeployTransaction(
		ADXTokenAddr,
		poolAddr,
		walletAddr
	).data
	const initCodeHash = utils.keccak256(initCode)

	const gaslessAddr = utils.getCreate2Address(
		ADDR_GASLESS_SWEEPER,
		Buffer.alloc(32),
		initCodeHash
	)

	return gaslessAddr
}
